
html {
  position: relative;
  min-height: 100%;
}

body {
  font-family: 'Roboto', sans-serif;
}


/* Изменения бутстрапа */
a {
  outline: none !important;
}

body.modal-open {
  overflow: inherit;
  padding-right: inherit !important;
}

.modal-content {
  border-radius: 0px;
  box-shadow: 0 0 4px rgba(0, 0, 0, 0.2);
}

.panel, .form-control, .alert {
  border-radius: inherit;
}

label, strong {
  font-weight: 500;
}

.jumbotron {
  background-color: #3a3f51;
  color: #a6a8b1;
}

/* !Изменения бутстрапа */


.btn-blue {
  border: none;
  border-radius: 0;
  color: #fff;
  background: #00afec;
  border: 1px solid transparent;
  -webkit-transition: .2s all;
  -o-transition: .2s all;
  transition: .2s all;
}

.btn-blue:hover {
  color: #00afec;
  background: #fff;
  border: 1px solid #00afec;
}


/* menuapp */

.menuapp {
  padding-top: 20px;
  padding-bottom: 20px;
}

.menuapp a:hover, .menuapp a.active {
  color: #00afec;
  text-decoration: none;
}

.menu-small, .menu-small a {
  font-size: 8pt;
  margin: 0px;
}


.menuapp h4 {
  font-weight: 300;
}

.menuapp .fa {
  font-size: 18pt;
}

/* Журнал действий */
.log-row {
  vertical-align: middle;
}

.subheaderlog, .subheaderlog > a {
  color: #c6c6c6;
  font-size: 16pt;
  font-weight: 200;
}


/*
 * Component: Small Box
 * --------------------
 */
.small-box {
  border-radius: 2px;
  position: relative;
  display: block;
  margin-bottom: 20px;
  box-shadow: 0 1px 1px rgba(0, 0, 0, 0.1);
}
.small-box > .inner {
  padding: 10px;
}
.small-box > .small-box-footer {
  position: relative;
  text-align: center;
  padding: 3px 0;
  color: #fff;
  color: rgba(255, 255, 255, 0.8);
  display: block;
  z-index: 10;
  background: rgba(0, 0, 0, 0.1);
  text-decoration: none;
}
.small-box > .small-box-footer:hover {
  color: #fff;
  background: rgba(0, 0, 0, 0.15);
}
.small-box h3 {
  font-size: 38px;
  font-weight: bold;
  margin: 0 0 10px 0;
  white-space: nowrap;
  padding: 0;
}
.small-box p {
  font-size: 15px;
}
.small-box p > small {
  display: block;
  color: #f9f9f9;
  font-size: 13px;
  margin-top: 5px;
}
.small-box h3,
.small-box p {
  z-index: 5;
}
.small-box .icon {
  -webkit-transition: all 0.3s linear;
  -o-transition: all 0.3s linear;
  transition: all 0.3s linear;
  position: absolute;
  top: -10px;
  right: 10px;
  z-index: 0;
  font-size: 90px;
  color: rgba(0, 0, 0, 0.15);
}
.small-box:hover {
  text-decoration: none;
  color: #f9f9f9;
}
.small-box:hover .icon {
  font-size: 95px;
}
@media (max-width: 767px) {
  .small-box {
    text-align: center;
  }
  .small-box .icon {
    display: none;
  }
  .small-box p {
    font-size: 12px;
  }
}

